import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';

class Catalog extends React.Component {
    constructor(props) {
        super(props);
        this.resetCounter = this.resetCounter.bind(this)

        this.app = this.props.parent;
        this.state = { "countDown": 30, handleKeys : ['up','down','enter','left']  }
        this.buttons = {}
        this.props.parent.currentPage = this;
        this.props.parent.setState({"availableKeys" : this.state.handleKeys})
    } 

    resetCounter(){
        this.setState({"countDown":30})
    }

    componentDidMount() {
        window.setElementEventLintener("Catalog")
        if (this.props.parent.state.cid)
            this.buttons[this.props.parent.state.cid].focus()
        else
            this.buttons[Object.keys(this.buttons)[0]].focus(); 

        var interval = window.setInterval(()=>{
            if ( this.state != null ){
                var newCountDown = this.state.countDown -1
                this.setState({ "countDown": newCountDown})
                if ( newCountDown === 0){
                    this.app.changePage( 1 )
                } 
            }
        }, 1000)

        this.setState({"interval":interval})
    }

    componentWillUnmount(){
        if (this.state.interval)
            window.clearInterval( this.state.interval )
    }

    render(){
        return <KeyboardEventHandler
                    handleKeys={this.state.handleKeys}
                    onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                    <div style={{backgroundImage:'url(./assets/bg_2.jpg)', width:1080, height:1920}}>
                        
                        <div className="catalog_dotImg" ></div>
                        <div className="catalog_bodyImage" style={{backgroundImage:'url(./assets/body.png)', width:261, height:1315}}></div>
                        <div className="catalog_shoulder"
                            tabIndex="1"
                            attr='{"cid":"19c32de9-5985-4e15-9905-bad3d932ae2d"}'
                            ref={(input) => { this.buttons["19c32de9-5985-4e15-9905-bad3d932ae2d"] = input; }}>
                            </div>
                        <div className="catalog_arm"
                            tabIndex="2"
                            attr='{"cid":"49a374be-7af0-4c46-bf31-cb905e71c250"}'
                            ref={(input) => { this.buttons["49a374be-7af0-4c46-bf31-cb905e71c250"] = input; }} >
                            </div>
                        <div className="catalog_back"
                            tabIndex="3"
                            attr='{"cid":"37f4487e-5b40-4c71-bd1b-a0cc6212fde1"}'
                            ref={(input) => { this.buttons["37f4487e-5b40-4c71-bd1b-a0cc6212fde1"] = input; }} >
                            </div>
                        <div className="catalog_hip"
                            tabIndex="4"
                            attr='{"cid":"e81a2288-6022-413d-ada2-375f17a467cb"}'
                            ref={(input) => { this.buttons["e81a2288-6022-413d-ada2-375f17a467cb"] = input; }} >
                            </div>
                        <div className="catalog_leg"
                            tabIndex="5"
                            attr='{"cid":"8bb8ca76-fb34-46b8-b781-3fc046d7e360"}'
                            ref={(input) => { this.buttons["8bb8ca76-fb34-46b8-b781-3fc046d7e360"] = input; }}></div>
                        
                        <div className="home_image1" style={{backgroundSize:82,width:82,height:82,top:1770,left:90}}></div>
                        <div className="home_textArea1" style={{top:1770,left:180,fontSize:26,width:110}}>
                                <div style={{color:"#17FF21"}}>點選</div>雙手彎舉
                        </div>

                        <div className="home_image2" style={{backgroundSize:82,width:82,height:82,top:1770,left:330}}></div>
                        <div className="home_textArea2" style={{top:1770,left:420,fontSize:26,width:110}}>
                            <div style={{color:"#17FF21"}}>前一頁</div>雙手交叉
                        </div>
                        

                        <div className="home_image3" style={{backgroundSize:82,width:82,height:82,top:1770,left:570}}></div>
                        <div className="home_textArea3" style={{top:1770,left:660,fontSize:26,width:110}}>
                            <div style={{color:"#17FF21"}}>向上選取</div>左手彎舉
                        </div>
                        

                        <div className="home_image4" style={{backgroundSize:82,width:82,height:82,top:1770,left:810}}></div>
                        <div className="home_textArea4" style={{top:1770,left:900,fontSize:26,width:110}}>
                            <div style={{color:"#17FF21"}}>向下選取</div>右手彎舉
                        </div>
                        
                    </div> 
                </KeyboardEventHandler>
    } 
}

export default Catalog;
