import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';

import {DataService} from '../services/DataService';

class ClassSelect extends React.Component {
    constructor(props) {
        super(props);
        this.resetCounter = this.resetCounter.bind(this)
        this.app = this.props.parent;
        this.props.parent.currentPage = this;
        this.state = { "countDown":30, "courseList":[], handleKeys : ['up','down','enter','left']}
        this.props.parent.setState({"availableKeys" : this.state.handleKeys})

        this.buttons={}
        this.countDown = 30
    } 

    resetCounter(){
        this.setState({"countDown":30})
    }

    async componentDidMount() {
        
        window.setElementEventLintener("ClassSelect")
        this.setState({"courseList": await DataService.getCourseList(this.props.parent.state.coachid,
                                                                      this.props.parent.state.cid) })

        let findButton = false;
        if (this.props.parent.state.ciid){
            if (this.buttons[this.props.parent.state.ciid]){
                this.buttons[this.props.parent.state.ciid].focus();
                findButton = true;
            }
        }

        if (findButton === false){
            var keys = Object.keys(this.buttons);
            if (keys.length > 0)
                this.buttons[keys[0]].focus(); 
            else {
                this.app.changePage( 2 )
                return;
            }
        }
            
        /*var interval = window.setInterval(()=>{
            if ( this.state != null ){
                var newCountDown = this.state.countDown -1
                this.setState({ "countDown": newCountDown})
                if ( newCountDown === 0){
                    this.app.changePage( 1 )
                }
            }
        }, 1000)

        this.setState({"interval":interval})*/
    }

    componentWillUnmount(){
        if (this.state.interval)
            window.clearInterval( this.state.interval )
    }

    async mycallback(title){
        if (this.app.state.isUseWebSocket){
            this.app.ws.send( JSON.stringify({"function":"uploadCourse", "name":encodeURI(title)} ))
        } else {
            try{
                var response = await fetch('http://127.0.0.1:5500/uploadCourse?name='+title, {
                    method: 'GET',
                    mode: 'cors',
                    //credentials: 'include',
                    crossDomain:true,
                    headers:{
                        Accept: '*/*',
                        'Content-Type': 'application/json;charset=UTF-8'
                    },
                });
                var body = await response.text();
                console.log( "uploadCourse:", body );
            }catch(error){
                console.log(error)
            }
        }
    }

    render(){
        return <KeyboardEventHandler
                    handleKeys={this.state.handleKeys}
                    onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                    <div style={{backgroundImage:'url(./assets/bg_2.jpg)', width:1080, height:1920}}>
                        <div className="classSelect_pageSize" ref={ref => {
                                                            this.page = ref;
                                                        }}  style={{maxHeight: (419+71)* this.state.courseList.length}}>
                            {
                                this.state.courseList.map((item,index) => {
                                    var attr = '{"ciid":"'+item.id+'"}'
                                    console.log(item)
                                    return <div 
                                        ref={(input) => { this.buttons[item.id] = input }} href="#"
                                        tabIndex={index}
                                        key={index}
                                        attr={attr}
                                        selectcallback="mycallback"
                                        selectcallbackparam={item.title}
                                        className="classSelect_pageButton"
                                        >
                                            <div className="classSelect_pageButton_title">{item.title}</div>
                                            <div className="classSelect_pageButton_line" ></div>
                                            <div className="classSelect_pageButton_imageSpace" >
                                                <img src={process.env.PUBLIC_URL + './course' + item.image} alt=""></img>
                                            </div>
                                            <div className="classSelect_pageButton_dot1"></div>
                                            <div className="classSelect_pageButton_dot1" style={{top:-70}}></div>
                                            <div className="classSelect_pageMuscle">訓練肌肉部位：</div>
                                            <div className="classSelect_pageMuscleData">{item.muscle}</div>
                                            <div className="classSelect_pageDescript">訓練成效：</div>
                                            <div className="classSelect_pageDescriptData">{item.description}</div>
                                        </div>
                                })
                            }
                        </div>
                        <div className="home_image1" style={{backgroundSize:82,width:82,height:82,top:1770,left:90}}></div>
                        <div className="home_textArea1" style={{top:1770,left:180,fontSize:26,width:110}}>
                                <div style={{color:"#17FF21"}}>點選</div>雙手彎舉
                        </div>

                        <div className="home_image2" style={{backgroundSize:82,width:82,height:82,top:1770,left:330}}></div>
                        <div className="home_textArea2" style={{top:1770,left:420,fontSize:26,width:110}}>
                            <div style={{color:"#17FF21"}}>前一頁</div>雙手交叉
                        </div>

                        <div className="home_image3" style={{backgroundSize:82,width:82,height:82,top:1770,left:570}}></div>
                        <div className="home_textArea3" style={{top:1770,left:660,fontSize:26,width:110}}>
                            <div style={{color:"#17FF21"}}>向上選取</div>左手彎舉
                        </div>
                        
                        <div className="home_image4" style={{backgroundSize:82,width:82,height:82,top:1770,left:810}}></div>
                        <div className="home_textArea4" style={{top:1770,left:900,fontSize:26,width:110}}>
                            <div style={{color:"#17FF21"}}>向下選取</div>右手彎舉
                        </div>

                    </div> 
                </KeyboardEventHandler>
    } 
}

export default ClassSelect;
